<template>
  <div class="login">
    <div class="content">
      <div class="login_logo">
        <img src="../../assets/logo.png" alt="#" />
      </div>
      <p class="name">BD运营后台</p>
      <div class="login_uesrInfo">
        <van-cell-group>
          <van-field
            v-model="userPhone"
            placeholder="请输入用户名"
            clearable
            type="tel"
            :error-message="pMsg"
          >
            <img src="..\..\assets\reg_icon\tel.png" slot="left-icon" />
          </van-field>
          <van-field
            v-model="password"
            type="password"
            placeholder="请输入密码"
            clearable
            :error-message="uMsg"
          >
            <img src="..\..\assets\reg_icon\mima.png" slot="left-icon" />
          </van-field>
        </van-cell-group>
      </div>
      <van-button
        type="info"
        size="normal"
        block
        :disabled="
          userPhone === '' ||
            password === '' ||
            pMsg !== '' ||
            uMsg !== '' ||
            loading
        "
        @click="fun_btn_login"
        >{{ $t("messages.login.btn_login") }}</van-button
      >
      <!-- <p class="login_regist" @click="fun_btn_regist">
        {{ $t("messages.login.login_regist") }}
      </p>
      <p class="login_forget" @click="fun_btn_reset">
        {{ $t("messages.login.forgetPWD") }}
      </p> -->
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import md5 from "md5";
import { setTimeout } from "timers";
export default {
  data() {
    return {
      userPhone: "",
      password: "",
      pMsg: "",
      uMsg: "",
      loading: false
    };
  },
  created() {
      if(this.$getBDUser()){
          this.$router.push({
              name: "bdHome"
          });
      }
  },
  methods: {
    fun_btn_login() {
      this.submit();
    },
    fun_btn_reset() {
      this.$router.push({
        name: "resetPassword"
      });
    },
    fun_btn_regist() {
      this.$router.push({
        name: "regist"
      });
    },
    submit() {
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        mask: true,
        message: "正在登陆中..."
      });
      JNB.query({
        serverName: "userServer",
        cls: "LoginRegistrationModule",
        method: "loginBack",
        params: {
          account: this.userPhone,
          password: md5(this.password),
          spicRole: 8192
        },
        callback: data => {
          try {
            let result = JSON.parse(data);
            if (result.code == 200) {
              this.$toast.success("登录成功");
              this.getUserInfo();
            } else {
              this.$toast.fail(result.message);
            }
          } catch (e) {
            this.$toast.fail("网络错误");
          }
        }
      });
    },
    getUserInfo() {
      JNB.query({
        serverName: "userServer",
        cls: "LoginRegistrationModule",
        method: "getUserSession",
        params: {},
        callback: data => {
          try {
            let result = JSON.parse(data);
            if (result.code == 200) {
              this.$putBDUser(JSON.stringify(result.data));
              // this.$toast.success("登录成功");
              this.$router.push({
                path: "/bdhome"
              });
            } else {
              this.$toast.fail(result.message);
            }
          } catch (e) {
            // console.log(e)
          }
        }
      });
    }
  },
  watch: {
    userPhone: function(newVal) {
      if (!/^1(3|4|5|6|7|8|9)\d{9}$/.test(newVal)) {
        this.pMsg = "请输入正确的手机号码！";
      } else {
        this.pMsg = "";
      }
    }
  }
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}
.login .login_logo {
  width: 268px;
  height: 72px;
  margin: 83px auto 0;
}
.login .login_logo img {
  width: 100%;
  height: 100%;
}
.login .login_uesrInfo .van-cell-group {
  padding: 20px 50px;
  /* margin-top: 30px; */
}
.login .login_uesrInfo .van-cell-group .van-field {
  width: 540px;
  height: 88px;
  font-size: 28px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 2px 19px 2px rgba(255, 234, 233, 0.4),
    0px 2px 3px 0px rgba(255, 234, 233, 0.63);
  border-radius: 10px;
  display: flex;
  align-items: center;
}
.login .login_uesrInfo .van-cell-group .van-field img {
  width: 50px;
  height: 50px;
  margin: 0 40px 0 22px;
}
.login .login_forget {
  float: right;
  margin: 40px 40px 0 0;
  font-size: 28px;
  color: rgba(102, 102, 102, 1);
}
.login .van-button {
  width: 540px;
  height: 88px;
  background: rgba(237, 47, 38, 1);
  border-radius: 8px;
  margin: 67px auto 0;
  border: none;
}
.login .login_regist {
  float: left;
  margin: 40px 0 0 40px;
  font-size: 28px;
  color: rgba(102, 102, 102, 1);
}
.login {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background: url("..\..\assets\bg.jpg") no-repeat;
  background-size: cover; */
}
.login .content {
  width: 620px;
  height: 748px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 20px 1px rgba(120, 62, 59, 0.54);
  border-radius: 12px;
  margin-top: -66px;
}
.van-hairline--top-bottom:after {
  border: 0px;
}
.van-cell {
  margin-top: 20px;
}
.login .content .name {
  text-align: center;
  padding: 30px 0 0;
  font-size: 30px;
  color: #666;
}
</style>
